.dialog_item {
    padding: 10px 0;
    border-bottom: 1px solid #ccc;
}

.dialog_item:hover {
    background-color: #f8f5f4;
}

.dialog_item a {
    text-decoration: none;
    display: flex;
    align-items: center;
}

.dialog_item img {
    width: 70px;
    border-radius: 50%;
    margin-right: 20px;
    margin-left: 10px;
}